import React, { Component } from 'react';

class Formulario extends Component {
    constructor() {
        super()
        this.state = {
            name: '',
            description: ''
        }

    }

    onChange = e => {
        this.setState({ [e.target.name]: e.target.value })
    }

    handleSubmit = e => {
        e.preventDefault();
        document.getElementById('nameInput').value = '';
        document.getElementById('descriptionInput').value = '';
        this.setState({ name: '', description: '' })
    }

    render = () => {

        return (
            <form onSubmit={this.handleSubmit}>
                <div className="row">

                    <div className="col-12 col-sm-3 ">
                        <input id='nameInput' onChange={this.onChange.bind(this)} name='name' type='text' className='form-control' placeholder='Nombre' required minLength='3' />
                    </div>

                    <div className="col-12 col-sm-7 my-3 my-sm-0">
                        <input id='descriptionInput' onChange={this.onChange.bind(this)} name='description' type='text' className='form-control' placeholder='Descripción' required minLength='5' />
                    </div>

                    <div className="col-12 col-sm-2 text-right ">
                        <button type='submit' className='btn btn-success mb-3 mr-lg-4 shadow-sm' onClick={() => this.props.addPost(this.state)}> Crear </button>
                    </div>
                </div>
            </form>
        );
    }
}

export default Formulario;