import React, { Component } from 'react';
import Formulario from './Formulario';
import Filtro from './Filtro';

class Lista extends Component {
	constructor() {
		super();
		this.state = {
			posts: [],
			postsAux: [],
			isLoaded: false
		}
	}

	componentWillMount = () => {
		fetch('http://localhost:3000/posts')
			.then(res => res.json())
			.then(json => {
				this.setState({
					posts: json.posts,
					isLoaded: true
				})
			})
	}

	render = () => {

		const { posts, isLoaded } = this.state;

		if (!isLoaded) {
			return <h2>Cargando....</h2>

		} else {
			return (
				<div>
					<Filtro searchPost={this.searchPost} ></Filtro>
					<table className="table mb-5 mt-2 table-striped text-center">
						<thead className="thead-dark">
							<tr>
								<th scope="col">Nombre</th>
								<th scope="col">Descripción</th>
								<th scope="col">Acción</th>
							</tr>
						</thead>
						<tbody>
							{
								posts.map(post => {
									return (
										<tr key={post.idposts} >
											<td>{post.name}</td>
											<td>{post.description}</td>
											<td>
												<button onClick={() => this.deletePost(post.idposts)} className="btn btn-danger btn-sm">Eliminar</button>
											</td>
										</tr>
									);
								})
							}
						</tbody>
					</table>
					<Formulario addPost={this.addPost} ></Formulario>
				</div>
			);
		}
	}

	deletePost = id => {
		let confirm = window.confirm('¿Estás seguro de que deseas eliminar este elemento?')
		if (confirm) {

			fetch(`http://localhost:3000/posts/${id}`, { method: 'DELETE' })
				.then(res => res.json())
				.then(data => {
					let newState = this.state.posts.filter(post => post.idposts !== id)
					this.setState({ posts: newState });
				});
		}
	}


	addPost = ({ name, description }) => {

		if (name.length >= 3 && description.length >= 5) {
			fetch('http://localhost:3000/posts', {
				method: 'POST',
				body: JSON.stringify({ name, description }),
				headers: { 'Content-type': "application/json" }
			})
				.then(res => res.json())
				.then(data => {
					let newState = this.state.posts;
					newState.push(data.post);
					this.setState({ posts: newState })
				});
		}
	}

	searchPost = ({ search }) => {
		if (search.length > 0) {
			this.setState({ postsAux: this.state.posts })
			let key = new RegExp(search, 'i')
			let newState = this.state.posts.filter(post => key.test(post.name))
			this.setState({ posts: newState });
		} else {
			this.setState({ posts: this.state.postsAux })
		}
	}
}

export default Lista;